import React from "react";
import PropTypes from "prop-types";

const Items = ({ data }) => (
  <ul className="wrp">
    {data.map((item, i) => (
      <li className="item" key={item.videoId}>
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={`https://www.youtube.com/watch?v=${item.videoId}`}
        >
          <img src={item.img} alt="aa" />
          <div className="overlay">
            <summary>
              <h2>{item.title}</h2>
              <h3>
                {item.author} @({item.timeStatus})
              </h3>
              <h3>觀看次數：{item.viewCountText}</h3>
              <h3>{item.publishedTimeText}</h3>
            </summary>
          </div>
        </a>
      </li>
    ))}
  </ul>
);

Items.propTypes = {
  data: PropTypes.array.isRequired
};

export default Items;
