const fetchYoutuberVideoListNextFunc = async (videoListJson, dispatch) => {
  if (
    videoListJson.data &&
    videoListJson.data[1].response.continuationContents.gridContinuation
      .continuations
  ) {
    const next =
      videoListJson.data[1].response.continuationContents.gridContinuation
        .continuations[0].nextContinuationData;
    dispatch(requestNext({ videoListJson }));
    const videoListNextJson = await fetchYoutuberVideoListNext({
      ctoken: next.continuation,
      continuation: next.continuation,
      itct: next.clickTrackingParams,
    });
    dispatch(receiveNext({ json: videoListNextJson }));
    return videoListNextJson;
    // .then(json => dispatch(receiveNext({ json })));
  }
};

const createYoutuber = (key, tag) => (dispatch) => {
  dispatch(request());
  fetchYoutuberVideoList(key).then((json) => {
    dispatch({ type: CREATE_REQUEST });
    axios({
      method: "POST",
      url: `https://me-cors.herokuapp.com/https://script.google.com/macros/s/AKfycbyZhZbdqsMYFA5_u_I1DzjpZphF4WlKZLmfhmEAZ56offNOGXU/exec`,
      params: { tag },
      data: {
        name: json.data[1].response.metadata.channelMetadataRenderer.title,
        id: key,
      },
    }).then((json) => {
      dispatch({ type: CREATE_RECEIVE });
    });
    dispatch(receive({ json }));
  });
};

export const fetchData = (params) => (dispatch) => {
  const pathParams = getJsonFromUrl();
  dispatch(fetchYoutuber(params || pathParams));
  dispatch({ type: CHANGE_TAG, tagData: params || pathParams });
};

export const fetchUData = (params) => (dispatch) => {
  const pathParams = getJsonFromUrl();
  const s = params || pathParams;
  s.u = "T";
  dispatch(fetchYoutuber(s));
  dispatch({ type: CHANGE_TAG, tagData: params || pathParams });
};

export const createData = (key, tag) => (dispatch) => {
  return dispatch(createYoutuber(key, tag));
};

export const clearData = () => (dispatch) => {
  return dispatch({ type: CLEAR_DATA });
};
