import { combineReducers } from "redux";
import {
  DATA_REQUEST,
  DATA_RECEIVE,
  DATA_REQUEST_NEXT,
  DATA_RECEIVE_NEXT,
  SET_SELECT,
  CREATE_REQUEST,
  CREATE_RECEIVE,
  CLEAR_DATA,
  CHANGE_TAG,
  DATA_SAVE,
} from "../actions";

const matchFunc = (o, regex, parse) => {
  if (o.match(regex)) {
    // console.log(o.match(regex));
    if (parse === false) {
      return o.match(regex)[0];
    } else {
      return parseInt(o.match(regex)[0]);
    }
  }
  return 0;
};

const handleTime = (timeText, tag) => {
  const year = matchFunc(timeText, /\d+(?= 年)/);
  const month = matchFunc(timeText, /\d+(?= 個月)/);
  const week = matchFunc(timeText, /\d+(?= 週)/);
  const day = matchFunc(timeText, /\d+(?= 天)/);
  const hour = matchFunc(timeText, /\d+(?= 小時)/);
  const min = matchFunc(timeText, /\d+(?= 分)/);
  const sec = matchFunc(timeText, /\d+(?= 秒)/);
  if (tag === "youtuber" && (year || month > 2)) {
    return null;
  }
  // console.log(year, month, week, day, hour, min, sec, timeText);
  return (
    +new Date() -
    ((((year * 365 + month * 30 + week * 7 + day) * 24 + hour) * 60 + min) *
      60 +
      sec) *
      1000
  );
};

const handleGetData = ({ data, tag }) => {
  return data.list.map((item) => {
    const { gridVideoRenderer } = item;
    // let publishedTime = handleTime(
    //   gridVideoRenderer.title.accessibility.accessibilityData.label,
    //   tag
    // );
    // console.log(
    //   gridVideoRenderer.thumbnailOverlays[0].thumbnailOverlayTimeStatusRenderer
    // );
    return {
      author: data.title,
      authorId: data.externalId,
      title: gridVideoRenderer.title.simpleText,
      videoId: gridVideoRenderer.videoId,
      img: gridVideoRenderer.thumbnail.thumbnails[0].url,
      viewCountText: matchFunc(
        (gridVideoRenderer.viewCountText &&
          gridVideoRenderer.viewCountText.simpleText) ||
          "",
        /[\d,]+/,
        false
      ),
      publishedTime: handleTime(
        gridVideoRenderer.title.accessibility.accessibilityData.label,
        tag
      ),
      publishedTimeText:
        (gridVideoRenderer.publishedTimeText &&
          gridVideoRenderer.publishedTimeText.simpleText) ||
        null,
      upcomingEventData:
        (gridVideoRenderer.upcomingEventData &&
          gridVideoRenderer.upcomingEventData.startTime) ||
        null,
      timeStatus:
        (gridVideoRenderer.thumbnailOverlays[0]
          .thumbnailOverlayTimeStatusRenderer &&
          gridVideoRenderer.thumbnailOverlays[0]
            .thumbnailOverlayTimeStatusRenderer.text.simpleText) ||
        null,
    };
  });
};

const handleData = ({ o, data, next, tag }) => {
  let tdata = {
    title: data.data.data[1].response.metadata.channelMetadataRenderer.title,
    externalId:
      data.data.data[1].response.metadata.channelMetadataRenderer.externalId,
  };
  if (next) {
    tdata = {
      ...tdata,
      list:
        data.data.data[1].response.continuationContents.gridContinuation.items,
    };
  } else {
    tdata = {
      ...tdata,
      list:
        data.data.data[1].response.contents.twoColumnBrowseResultsRenderer
          .tabs[1].tabRenderer.content.sectionListRenderer.contents[0]
          .itemSectionRenderer.contents[0].gridRenderer.items,
    };
  }

  const newData = handleGetData({ data: tdata, tag }).filter((i) => {
    // console.log(i.timeStatus);
    if (
      (tag === "show" || tag === "legend") &&
      i.timeStatus !== null &&
      i.timeStatus.indexOf(":") > -1
    ) {
      let sec = 0;
      const time =
        i.timeStatus.match(/(\d+)(?::(\d+))(?::(\d+))/) ||
        i.timeStatus.match(/(\d+)(?::(\d+))/);
      // console.log(time);
      if (time.length === 4) {
        sec =
          (parseInt(time[1]) * 60 + parseInt(time[2])) * 60 + parseInt(time[3]);
      }
      if (time.length === 3) {
        sec = parseInt(time[1]) * 60 + parseInt(time[2]);
      }
      // console.log(sec);
      if (tag === "legend") return sec > 10 * 60;
      return sec > 40 * 60;
    }
    return i.publishedTime !== null && i.upcomingEventData === null;
  });
  return []
    .concat(o)
    .concat(newData)
    .sort((a, b) => {
      return b.publishedTime - a.publishedTime;
    });
};

const handleSelectData = (o, data) => {
  const title =
    data.data.data[1].response.metadata.channelMetadataRenderer.title;
  const id =
    data.data.data[1].response.metadata.channelMetadataRenderer.externalId;
  const newData = [{ value: id, label: `${o.length + 1}. ${title}` }];
  return [].concat(o).concat(newData);
};

const handlePSelectData = (o, data) => {
  const a = data.reduce((a, b) => {
    a[b.authorId] = b.author;
    return a;
  }, {});
  // const title =
  //   data.data.data[1].response.metadata.channelMetadataRenderer.title;
  // const id =
  //   data.data.data[1].response.metadata.channelMetadataRenderer.externalId;
  const newData = Object.entries(a).map((i, index) => ({
    value: i[0],
    label: `${index + 1}. ${i[1]}`,
  }));
  return [].concat(o).concat(newData);
};

const data = (
  state = {
    isFetching: false,
    fetchingNum: 0,
    y2bData: [],
    y2bSelect: [],
    y2bSelected: [],
    tag: "youtuber",
  },
  action
) => {
  switch (action.type) {
    case DATA_REQUEST:
    case DATA_REQUEST_NEXT:
    case CREATE_REQUEST:
      return {
        ...state,
        isFetching: true,
        fetchingNum: state.fetchingNum + 1,
      };
    case DATA_SAVE:
      return {
        ...state,
        isFetching: false,
        fetchingNum: state.fetchingNum - 1,
        y2bData: action.data.data,
        y2bSelect: handlePSelectData(state.y2bSelect, action.data.data),
      };
    case DATA_RECEIVE:
      // console.log(action.data);
      if (action.data.data.reload === "now") {
        return {
          ...state,
          isFetching: false,
          fetchingNum: state.fetchingNum - 1,
        };
      }
      return {
        ...state,
        isFetching: false,
        fetchingNum: state.fetchingNum - 1,
        y2bData: handleData({ o: state.y2bData, data: action, tag: state.tag }),
        y2bSelect: handleSelectData(state.y2bSelect, action),
      };
    case DATA_RECEIVE_NEXT:
      return {
        ...state,
        isFetching: false,
        fetchingNum: state.fetchingNum - 1,
        y2bData: handleData({
          o: state.y2bData,
          data: action,
          next: true,
          tag: state.tag,
        }),
      };
    case CREATE_RECEIVE:
      return {
        ...state,
        isFetching: false,
        fetchingNum: state.fetchingNum - 1,
      };
    case SET_SELECT:
      return {
        ...state,
        y2bSelected: action.data,
      };
    case CLEAR_DATA:
      return {
        isFetching: false,
        fetchingNum: 0,
        y2bData: [],
        y2bSelect: [],
        y2bSelected: [],
      };
    case CHANGE_TAG:
      if (action.tagData) {
        return {
          ...state,
          tag: action.tagData.tag,
        };
      }
    // eslint-disable-next-line
    default:
      return state;
  }
};

const rootReducer = combineReducers({
  data,
});

export default rootReducer;
