import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { fetchData, fetchUData, createData, clearData } from "../actions";
// import Picker from "../components/Picker";
import Items from "../components/Items";
// import Select from "react-select";
import CreatableSelect from "react-select/lib/Creatable";
import makeAnimated from "react-select/lib/animated";

class App extends Component {
  static propTypes = {
    // isFetching: PropTypes.bool.isRequired,
    dispatch: PropTypes.func.isRequired,
  };

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(fetchData());
  }

  // componentDidUpdate(prevProps) {
  //   if (prevProps.selectedSubreddit !== this.props.selectedSubreddit) {
  //     const { dispatch, selectedSubreddit } = this.props
  //     dispatch(fetchPostsIfNeeded(selectedSubreddit))
  //   }
  // }

  handleChange = (data) => {
    const { dispatch } = this.props;
    dispatch({ type: "SET_SELECT", data: data.map((item) => item.value) });
    // console.log(data);
  };

  handleCreate = (inputValue) => {
    const { dispatch, tag } = this.props;
    dispatch(createData(inputValue, tag));
  };

  handleReload = () => {
    const { dispatch } = this.props;
    dispatch(clearData());
    dispatch(fetchData());
  };

  handleChangeTag = (tag) => {
    const { dispatch } = this.props;
    dispatch(clearData());
    dispatch(fetchData({ tag }));
  };

  handleChangeUTag = (tag) => {
    const { dispatch } = this.props;
    dispatch(clearData());
    dispatch(fetchUData({ tag }));
  };

  // handleRefreshClick = e => {
  //   e.preventDefault()

  //   const { dispatch, selectedSubreddit } = this.props
  //   dispatch(invalidateSubreddit(selectedSubreddit))
  //   dispatch(fetchPostsIfNeeded(selectedSubreddit))
  // }

  render() {
    const { fetchingNum, y2bData, y2bSelect, y2bSelected } = this.props;
    let now = y2bData;
    if (y2bSelected.length > 0) {
      now = y2bData.filter((item) => y2bSelected.indexOf(item.authorId) > -1);
    }
    return (
      <div>
        <CreatableSelect
          placeholder="請選擇需要的 Youtuber"
          options={y2bSelect}
          components={makeAnimated()}
          closeMenuOnSelect={false}
          isMulti
          onChange={this.handleChange}
          isClearable
          onCreateOption={this.handleCreate}
          // isDisabled={fetchingNum > 0}
          isLoading={fetchingNum > 0}
        />
        <div className="btns">
          <button disabled={fetchingNum > 0} onClick={this.handleReload}>
            重整
          </button>
          <button
            disabled={fetchingNum > 0}
            onClick={() => this.handleChangeTag("youtuber")}
          >
            youtuber
          </button>
          <button
            disabled={fetchingNum > 0}
            onClick={() => this.handleChangeTag("show")}
          >
            show
          </button>
          <button
            disabled={fetchingNum > 0}
            onClick={() => this.handleChangeTag("legend")}
          >
            legend
          </button>
          <button
            disabled={fetchingNum > 0}
            onClick={() => this.handleChangeTag("rare")}
          >
            rare
          </button>
        </div>
        <Items data={now} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  const {
    data: { y2bData, y2bSelect, y2bSelected, fetchingNum, tag },
  } = state;

  return {
    y2bData,
    y2bSelect,
    y2bSelected,
    fetchingNum,
    tag,
  };
};

export default connect(mapStateToProps)(App);
